import * as React from 'react';
import * as Konva from 'konva';
import {Stage, Layer} from 'react-konva';

import BoardFieldComponent from './BoardField';

export interface Point {
  X: number;
  Y: number;
}

export interface Field {
  color: string;
  coords: Point;
}

var boardDimension: Point = {X: 8, Y: 8};
var canvasDimension: Point = {X: 800, Y: 800};

class GameBoard extends React.Component {  
  render() {
    var fields = getInitialFields();
    const fieldComponents = fields.map((curField) => <BoardFieldComponent color={curField.color} rect={layouter(boardDimension, canvasDimension, curField.coords)} />
  );

    return (
      <div className="App">
        <Stage width={canvasDimension.X} height={canvasDimension.Y} >
        <Layer>
          {fieldComponents}
        </Layer>
      </Stage>
      </div>
    );
  }
}

export default GameBoard;

// helpers

function layouter(boardDimension: Point,
                  canvasDimension: Point,
                  fieldLocation: Point) {
  return{
          X: canvasDimension.X/boardDimension.X*fieldLocation.X,
          Y: canvasDimension.Y/boardDimension.Y*fieldLocation.Y,
          Width: canvasDimension.X/boardDimension.X,
          Height: canvasDimension.Y/boardDimension.Y
        };
}

function getInitialFields() : Field[] {
  const fields:Field[] = get2dCoords(boardDimension).map((curField) => {return {color: Konva.Util.getRandomColor(), coords: curField}});
  return fields;
}

function get2dCoords(dim: Point) : Point[] {
  var arr: Point[] = new Array(dim.X * dim.Y);
  var c=0;
  for (var i = 1; i <= dim.X; i++) {
    for (var j = 1; j <= dim.Y; j++) {
      var X = i-1;
      var Y = j-1;
      arr[c] = {X,Y};
      c++;
    }    
  }
  return arr;
}



