import * as React from 'react';
import * as Konva from 'react-konva';

export interface Rect {
  X: number;
  Y: number;
  Width: number;
  Height: number;
}

export interface Props {
  color: string;
  rect: Rect;
}

function BoardFieldComponent({ color = 'white', rect }: Props) {
  return (
    <Konva.Rect
      x={rect.X}
      y={rect.Y}
      width={rect.Width}
      height={rect.Height}
      fill={color}
      shadowBlur={5}
      opacity={0.5}
    />
  );
}

export default BoardFieldComponent;


